function ResponsiveAppBar() {
  const theme = useTheme()
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <AppBar color="primary" position="sticky">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            LOGO
          </Typography>
          <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Priam
          </Typography>

          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Open settings">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                <Profile/>
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}

function SpinnerMenu()
{
  const [loginLocation, setLoginLocation] = useState(window.location.origin)
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const navigate = useNavigate();
  const navigateSite = (link) =>{
    navigate(link);
  }
  const [selected, setSelected] = useState(0)
  const { logout } = useAuth0();
  return (
    <SpeedDial
          ariaLabel="SpeedDial example"
          sx={{position: 'fixed', width:'fit-content',height:'fit-content', bottom: 0,right:0}}
          icon={<SpeedDialIcon />}
          onClose={handleClose}
          onOpen={handleOpen}
          open={open}
          FabProps = {{
            sx:{
              bgcolor:'#193A6F',
              transition:'all 0.6s'
            }
          }}
        >
          <SpeedDialAction 
          key="Home"
          icon={<HomeIcon />}
          tooltipTitle="Home"
          tooltipOpen
          onClick={()=> {navigate({pathname:'/dashboard'})}}
          />
          <SpeedDialAction
            key="About"
            icon={<InfoIcon />}
            tooltipTitle="About"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/about'})}}
            />
          <SpeedDialAction
            key="Search"
            icon={<SearchIcon />}
            tooltipTitle="Search"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/search'})}}
            />
            <SpeedDialAction
            key= "New Post"
            icon={<FiberNewIcon />}
            tooltipTitle="New Post"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/newpost'})}}
            />
          <SpeedDialAction
          key = "Logout"
          icon={<LogoutIcon />}
          tooltipTitle="Logout"
          tooltipOpen
          onClick={()=> {logout({returnTo:window.location.origin})}}
          />
        </SpeedDial>
  )
}

